.body {
  display: flex;
  gap: 30px;
  justify-content: center;
  margin-bottom: 50px;
  & .item {
    cursor: pointer;
    & a {
      display: flex;
      align-items: center;
      justify-content: center;
      font-family: "Nunito Sans";
      font-weight: 700;
      font-size: calc(14px + 2 * (100vw / 1200));
      color: rgba(#000, 0.5);
      width: 30px;
      height: 30px;
    }
  }
  & .toggle a {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 30px;
    background: none;
    cursor: pointer;
    & img {
      width: 15px;
      height: 15px;
    }
  }
  & .item.active a {
    color: rgba(#000, 1);
    border-bottom: 1px solid #000;
  }
}
