.body {
  position: fixed;
  top: 0;
  left: 0;
  width: 40vw;
  height: 100vh;
  background: #f7f7f7;
  padding: 40px 20px;
  overflow-y: auto;
  z-index: 3;
  .closeMenuButton {
    position: absolute;
    top: 42px;
    right: 42px;
    background: none;
    img {
      width: 20px;
      height: 20px;
    }
    &:hover {
      opacity: 0.7;
    }
  }
  &::-webkit-scrollbar {
    width: 5px;
  }
  &::-webkit-scrollbar-thumb {
    background: rgb(233, 231, 231);
    border-radius: 999px;
  }
  .logo {
    width: 100%;
    display: block;
    text-align: center;
  }
  .nav {
    margin-top: 40px;
    .list {
      display: flex;
      flex-direction: column;
      gap: 20px;
      .item {
        .link {
          display: flex;
          align-items: center;
          justify-content: space-between;
          font-family: "Poppins";
          font-style: normal;
          font-weight: 600;
          font-size: calc(16px + 2 * (100vw / 1200));
          line-height: 30px;
          height: 30px;
          transition: all 0.2s ease;
          &:hover {
            opacity: 0.7;
          }
        }
      }
    }
  }
}

@media (max-width: 767px) {
  .body {
    width: 100vw;
  }
}
