.search {
  display: flex;
  align-items: center;
  .wrapp {
    display: flex;
    align-items: flex-end;
    position: relative;
    height: 40px;
    border-bottom: 1px solid #000;
    .input {
      font-family: "Raleway";
      font-weight: 600;
      font-size: calc(14px + 2 * (100vw / 1200));
      line-height: 21px;
      margin-left: 40px;
      height: 80%;
      color: #000;
      &::placeholder {
        font-weight: 400;
        color: rgba(0, 0, 0, 0.8);
      }
    }
    .searchIcon {
      position: absolute;
      width: 15px;
      height: 15px;
      top: 15px;
      left: 10px;
    }
    .clearInputButton {
      display: none;
    }
  }
}

@media (max-width: 767px) {
  .search {
    .wrapp {
      position: relative;
      .clearInputButton {
        display: block;
        position: absolute;
        top: 15px;
        right: 20px;
        background: none;
        font-family: "Nunito Sans";
        font-weight: 600;
        font-size: calc(14px + 2 * (100vw / 1200));
        text-decoration: underline;
      }
    }
  }
}
