.body {
  position: relative;
  width: 100%;
  padding: 15px;
  border: 1.2px solid rgba(0, 0, 0, 0.1);
  border-left: none;
  border-right: none;
  .price {
    position: absolute;
    right: 5%;
    bottom: 12%;
    font-family: "Nunito Sans";
    font-weight: 700;
    font-size: calc(14px + 4 * (100vw / 1200));
    line-height: normal;
    color: #000000;
  }
  .info {
    display: flex;
    align-items: center;
    gap: 5vw;
    .image {
      width: 15%;
      height: 100%;
      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        object-position: center;
      }
    }
    .wrapp {
      display: flex;
      flex-direction: column;
      gap: 1em;
      .title {
        font-family: "Nunito Sans";
        font-style: normal;
        font-weight: 700;
        font-size: calc(16px + 4 * (100vw / 1200));
        line-height: normal;
        color: #000000;
      }
      .bottom {
        display: flex;
        .edit {
          .editList {
            display: flex;
            gap: 5vw;
            .editListItem {
              display: flex;
              align-items: center;
              gap: 5px;
              font-family: "Nunito Sans";
              font-weight: 600;
              font-size: calc(14px + 2 * (100vw / 1200));
              line-height: normal;
              text-decoration: underline;
              color: rgba(0, 0, 0, 0.75);
              cursor: pointer;
            }
          }
        }
      }
    }
  }
}

@media (max-width: 480px) {
  .body {
    .info {
      display: block;
      .image {
        display: block;
        margin: 0px auto;
        width: 150px;
        height: 150px;
      }
    }
  }
}
