.body {
  .button {
    display: flex;
    align-items: center;
    gap: 8px;
    font-family: "Nunito Sans";
    font-weight: 600;
    font-size: calc(14px + 2 * (100vw / 1200));
    line-height: normal;
    color: #000000;
    background: none;
    cursor: pointer;
    .arrow {
      transition: rotate 0.3s ease;
    }
    .arrow.active {
      rotate: 180deg;
    }
  }
  .poppup {
    position: absolute;
    right: 0;
    top: 30px;
    z-index: 5;
    .list {
      width: calc(80px + 40 * (100vw / 1200));
      .item {
        background: #f5f5f5;
        display: flex;
        align-items: center;
        justify-content: center;
        font-family: "Nunito Sans";
        font-weight: 400;
        font-size: calc(14px + 2 * (100vw / 1200));
        line-height: normal;
        color: rgba(0, 0, 0, 0.8);
        padding: 5px 0px;
        border: 1px solid rgba(0, 0, 0, 0);
        cursor: pointer;
        &:hover:not(.active) {
          color: rgba(0, 0, 0, 1);
          background: #f0f0f0;
        }
      }
      .item.active {
        border: 1px solid rgba(0, 0, 0, 0.5);
        background: #f0f0f0;
        color: rgba(0, 0, 0, 1);
      }
    }
  }
}
@media (max-width: 1024px) {
  .body {
    top: 25px;
  }
}
