.categories {
  .button {
    display: flex;
    align-items: center;
    gap: 8px;
    font-family: "Nunito Sans";
    font-weight: 600;
    font-size: calc(14px + 2 * (100vw / 1200));
    line-height: normal;
    color: #000000;
    background: none;
  }
  .poppup {
    position: absolute;
    top: 35px;
    .list {
      display: flex;
      .item {
        font-family: "Nunito Sans";
        font-style: normal;
        font-weight: 600;
        font-size: calc(12px + 4 * (100vw / 1200));
        line-height: normal;
        cursor: pointer;
        padding: 3px 15px;
        background: #f5f5f5;
        color: rgba(0, 0, 0, 0.8);
        border: 1px solid rgba(0, 0, 0, 0);
        &:hover:not(.active) {
          background: #f0f0f0;
          color: rgba(0, 0, 0, 1);
        }
      }
      .item.active {
        border: 1px solid rgba(0, 0, 0, 0.5);
        background: #f0f0f0;
        color: rgba(0, 0, 0, 1);
      }
    }
  }
}
