@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.loading {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 48px;
  height: 48px;
  transform: translate(-50%, -50%);
  .spinner {
    position: absolute;
    left: 1px;
    top: 1px;
    width: 46px;
    height: 46px;
    animation: spin 1s infinite linear;
    .mask {
      width: 32px;
      height: 32px;
      overflow: hidden;
      .maskedCircle {
        width: 41px;
        height: 41px;
        border-radius: 50%;
        border: 3px solid #000;
      }
    }
  }
}
