.body {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  background: #fff;
  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 80px;
    border-bottom: 1.5px solid rgba(0, 0, 0, 0.3);
    .button {
      transition: opacity 0.2s ease;

      &:hover {
        opacity: 0.7;
      }
    }
    .leftWrapp {
      display: flex;
      align-items: center;
      gap: 25px;
      .buttonMenu {
        cursor: pointer;
        background: none;
        user-select: none;
      }
    }
    .rightWrapp {
      user-select: none;
      display: flex;
      align-items: center;
      gap: 30px;
      .search {
        .input {
          width: calc(120px + 180 * (100vw / 1200));
        }
      }
      .searchButton {
        background: none;
        transition: opacity 0.2s ease;

        &:hover {
          opacity: 0.7;
        }
      }
    }
  }
}

@media (max-width: 767px) {
  .body {
    .container {
      height: 60px;
      .rightWrapp {
        .search {
          display: none;
        }
        .searchButton {
          display: none;
        }
      }
    }
  }
}
@media (max-width: 480px) {
  .body {
    .container {
      height: 50px;
    }
  }
}
