@import url("./reset.scss");
body {
  &::-webkit-scrollbar {
    width: 0.4vw;
  }
  &::-webkit-scrollbar-thumb {
    background: rgba(0, 0, 0, 0.9);
  }
}
.wrapper {
  min-height: 100%;
  overflow: hidden;
}
.container {
  max-width: 1200px;
  margin: 0px auto;
  overflow: hidden;
  padding: 0px 15px;
}

.search-enter {
  opacity: 0;
  transform: translate(20px, 0px);
}
.search-enter-active {
  opacity: 1;
  transform: translate(0px, 0px);
  transition: opacity 300ms, transform 300ms;
}
.search-exit {
  opacity: 1;
}
.search-exit-active {
  opacity: 0;
  transform: translate(20px, 0px);
  transition: opacity 300ms, transform 300ms;
}

.overlay-enter {
  opacity: 0;
}
.overlay-enter-active {
  opacity: 1;
  transition: opacity 300ms;
}
.overlay-exit {
  opacity: 1;
}
.overlay-exit-active {
  opacity: 0;
  transition: opacity 300ms;
}

.menu-enter {
  opacity: 0;
  transform: translate(-25%, 0px);
}
.menu-enter-active {
  opacity: 1;
  transform: translate(0%, 0px);
  transition: opacity 300ms, transform 400ms;
}
.menu-exit {
  opacity: 1;
}
.menu-exit-active {
  opacity: 0;
  transform: translate(-25%, 0px);
  transition: opacity 300ms, transform 300ms;
}

.sort-enter {
  opacity: 0.5;
  transform: translate(0px, -10%);
}
.sort-enter-active {
  opacity: 1;
  transform: translate(0px, 0%);
  transition: opacity 300ms, transform 300ms;
}
.sort-exit {
  opacity: 0.5;
}
.sort-exit-active {
  opacity: 0;
  transform: translate(0px, -10%);
  transition: opacity 300ms, transform 300ms;
}

.categories-enter {
  opacity: 0.5;
  transform: translate(0px, -10%);
}
.categories-enter-active {
  opacity: 1;
  transform: translate(0px, 0%);
  transition: opacity 300ms, transform 300ms;
}
.categories-exit {
  opacity: 0.5;
}
.categories-exit-active {
  opacity: 0;
  transform: translate(0px, -10%);
  transition: opacity 300ms, transform 300ms;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.15);
  z-index: 2;
}

.section-title {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-size: calc(20px + 2 * (100vw / 1200) + (100vw / 320));
  line-height: normal;
  letter-spacing: 0.05em;
  color: #000000;
}
