.body {
  font-family: "Nunito Sans";
  padding: 20px;
  cursor: pointer;
  transition: box-shadow 0.3s ease, transform 0.3s ease;
  border-radius: 2px;
  &:hover {
    transform: translate(0px, -2px);
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.05);
  }
  .image {
    display: block;
    max-width: fit-content;
    max-height: fit-content;
    margin: 0px auto 20px;
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: center;
    }
  }
  .preloaderImage {
    position: relative;
    overflow: hidden;
    &::before {
      content: "";
      position: absolute;
      left: 0px;
      top: 0px;
      width: 50%;
      height: 100%;
      background: #000;
      z-index: 2;
      animation-name: preloader-animation;
      animation-duration: 1s;
      animation-iteration-count: infinite;
      animation-direction: alternate;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: center;
    }
  }
  .title {
    font-weight: 700;
    font-size: 18px;
    line-height: 25px;
    margin-bottom: 10px;
  }
  .price {
    font-weight: 700;
    font-size: 18px;
    line-height: 25px;
    color: #000000;
  }

  @keyframes spin {
    to {
      transform: rotate(360deg);
    }
  }
  @keyframes preloader-animation {
    from {
      background: linear-gradient();
      transform: translateX(-100%);
      background: linear-gradient(
        90deg,
        rgba(0, 0, 0, 0.005),
        rgba(0, 0, 0, 0.006),
        rgba(0, 0, 0, 0.007)
      );
    }
    to {
      transform: translateX(180%);
      background: linear-gradient(
        90deg,
        rgba(0, 0, 0, 0.005),
        rgba(0, 0, 0, 0.006),
        rgba(0, 0, 0, 0.007)
      );
    }
  }
}
