.main {
  margin-top: 140px;
  .container {
    position: relative;
    .refinements {
      .filter {
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-left: none;
        border-right: none;
        margin-bottom: 70px;
      }
    }
    .searchWrapp {
      .search {
        display: none;
        .input {
          width: 100%;
        }
      }
    }
    .notFoundBySearchValue {
      font-family: "Nunito Sans";
      font-weight: 700;
      font-size: calc(16px + 4 * (100vw / 1200));
      display: flex;
      gap: 10px;
      margin-top: 40px;
    }
    .items {
      margin-top: 50px;
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      row-gap: 30px;
      margin-bottom: 100px;
    }
  }
}

@media (min-width: 1200px) {
  .main {
    margin-top: 130px;
  }
}
@media (max-width: 1024px) {
  .main {
    margin-top: 120px;
    .container {
      .items {
        grid-template-columns: repeat(3, 1fr);
        margin-top: 35px;
      }
    }
  }
}
@media (max-width: 767px) {
  .main {
    margin-top: 90px;
    .container {
      .refinements {
        .searchWrapp {
          display: flex;
          flex-direction: column-reverse;
          gap: 20px;
          .search {
            display: block;
          }
        }
      }
      .items {
        grid-template-columns: repeat(2, 1fr);
        margin-top: 80px;
      }
    }
  }
}
@media (max-width: 480px) {
  .main {
    margin-top: 80px;
    .container {
      .items {
        grid-template-columns: repeat(1, 1fr);
      }
    }
  }
}
